import { API, services } from "@miq/admin";
import { getService } from "@miq/utils";
import { addOrUpdateArrayObject } from "@miq/utils";

export const docServices = {
    path: "documents/",

    /**
     * DOCUMENT
     */

    list(params) {
        return getService(API, this.path, params);
    },
    listPage(url) {
        return getService(API, url);
    },
    get(docSlug, params) {
        return getService(API, `${this.path}${docSlug}/`, params);
    },
    post(values) {
        return services.post(`${this.path}`, values);
    },
    patch(docSlug, values, oldValues) {
        return services.patch(`${this.path}${docSlug}/`, values, oldValues);
    },
    archive(docSlug) {
        return services.get(`${this.path}${docSlug}/archive/`);
    },
    delete(docSlug) {
        return services.delete(`${this.path}${docSlug}/`);
    },

    /**
     * USER
     */

    patchUserPermission(docSlug, user_slug, permission) {
        return services.post(`${this.path}${docSlug}/permission/`, { user_slug, permission });
    },

    getStaffList(q) {
        return services.get("search-staff/", { q });
    },

    patchUser(docSlug, values) {
        if (!Object.keys(values).includes("action")) {
            return Promise.reject("Action required");
        }

        return services.post(`${this.path}${docSlug}/users/`, values);
    },
};

export const docsReducer = (state = { results: [] }, action) => {
    const { type, payload = {} } = action;
    let results;

    switch (type) {
        case "SET_DOCS":
            return { ...payload };

        case "APPEND_DOC":
            results = addOrUpdateArrayObject(state.results, payload, "slug", true);
            return { ...state, results };

        case "ADD_UPDATE_DOC":
        case "PREPEND_DOC":
            results = addOrUpdateArrayObject(state.results, payload);
            return { ...state, results };

        case "REMOVE_DOC":
            results = state.results.filter(({ slug }) => slug !== payload.slug);
            return { ...state, results };

        default:
            return { ...state };
    }
};
